import { Helmet } from 'react-helmet-async';

import { 
  Routes,
  Route,
  Navigate
} from 'react-router-dom';

import './styles/colors.css';
import './styles/typography.css';

import './styles/components/grid-system.css';

import './styles/elements/buttons.css';
import './styles/elements/inputs.css';

import Sidebar from './components/Sidebar/Sidebar';

import HomePage from './pages/HomePage/HomePage';
import NotFoundPage from './pages/NotFoundPage/NotFoundPage';
import SuburbsPage from './pages/SuburbsPage/SuburbsPage';
import SuburbsIndex from './pages/SuburbsIndex/SuburbsIndex';
import SuburbProfilePage from './pages/SuburbProfilePage/SuburbProfilePage';
import FAQPage from './pages/FAQPage/FAQPage';

import redirectTable from './meta/redirect-table';

import './App.css';

function App() {
  return (
    <div className="App">
      <Helmet>
        <title>Crime Map of Australia - RedSuburbs</title>
        <meta name="description" content="Explore crime map of Australia based on police data and easy to use." />

        <meta property="og:locale" content="en_AU" />
        <meta property="og:type" content="website" />
        <meta property="og:site_name" content="RedSuburbs" />
        <meta property="og:title" content="Crime Map of Australia" />
        <meta property="og:description" content="Explore online crime map of Australia based on police data" />
        <meta property="og:url" content="https://redsuburbs.com.au" />
        <meta property="og:image:width" content="1200" />
        <meta property="og:image:height" content="630" />
        <meta property="og:image"
              content="https://redsuburbs.com.au/images/redsuburbs-image.jpg" />

        <meta name="twitter:card" content="summary_large_image" />
        <meta name="twitter:description" content="Explore online crime map of Australia based on police data" />
      </Helmet>

      <Routes>
        <Route path="/" element={<HomePage />} />
        <Route path="/map" element={<Navigate replace to="/" />} />
        <Route path="/suburbs" element={<SuburbsPage />} />
        <Route path="/suburbs-index" element={<SuburbsIndex />} />
        {Object.keys(redirectTable).map(oldPath => <Route path={oldPath} element={ <Navigate replace to={redirectTable[oldPath]} /> } /> )}
        <Route path="/suburbs/:key" element={<SuburbProfilePage />} />
        <Route path="/faq" element={<FAQPage />} />
        <Route path="*" element={<NotFoundPage />} />
      </Routes>
      <Sidebar />
    </div>
  );
}
/* test */
export default App;
