import React, {
  useState,
  useEffect
} from 'react';
import { 
  Link,
  useNavigate,
} from 'react-router-dom';
import { Helmet } from 'react-helmet-async';

import { 
  getSuburbMapLink,
  getSuburbProfileLink,
  useQuery
} from '../../Utils';

import ContentPageHeader from '../../components/ContentPageHeader/ContentPageHeader';
import ContentPageFooter from '../../components/ContentPageFooter/ContentPageFooter';

import ISuburb from '../../interfaces/ISuburb'; 

import './SuburbsPage.css';

type SuburbSortKey = 'name' | 'cr' | 'pops';

function sortSuburbs(s1: ISuburb, s2: ISuburb, key: SuburbSortKey, asc: boolean) {
  const ascMult = asc ? 1 : -1;
  const t1 = key === 'name' ? s1[key].toUpperCase() : s1[key];
  const t2 = key === 'name' ? s2[key].toUpperCase() : s2[key];
  return (t1 < t2) ? -1 * ascMult : (t1 > t2) ? 1 * ascMult : 0;
}

export default function SuburbsPage() {
  const query = useQuery();
  const navigate = useNavigate();
  let sortKey: SuburbSortKey = query.has('sort_key') ? query.get('sort_key') as SuburbSortKey : 'pops';
  let sortAsc: boolean = query.has('sort_asc') ? query.get('sort_asc') === 'true' : true;
  const perPage = 20;
  let page: number = query.has('page') ? parseInt(query.get('page') as string) : 1;
  if(page < 1) page = 1;
  const [suburbs, setSuburbs] = useState<ISuburb[]>([]);
  //const [sortKey, setSortKey] = useState<SuburbSortKey>('pops');
  //const [sortAsc, setSortAsc] = useState<boolean>(true);

  useEffect(() => {
    (async () => {
      const response = await fetch('/data/suburbs.json');
      if(!response.ok) {
        alert('Failed to load suburbs list!');
        throw new Error('Failed to load suburbs list!');
      }

      const data: ISuburb[] = await response.json();
      setSuburbs(data);
    })(); 
  }, []);

  function changeSortKey(key: SuburbSortKey) {
    if(key !== sortKey) {
      //setSortKey(key);
      sortKey = key;
      sortAsc = true;
      //setSortAsc(true);
    } else {
      //setSortAsc(oldVal => !oldVal);
      sortAsc = !sortAsc;
    }
    changePage(0);
  }

  function changePage(delta: number) {
    let nextPage = page + delta;
    if(nextPage < 1) nextPage = 1; 
    navigate(`?sort_key=${sortKey}&sort_asc=${sortAsc}&page=${nextPage}`);
  }

  return (
    <div className="SuburbsPage Page">
      <Helmet>
        <title>Australia Suburbs List - RedSuburbs</title>
        <meta name="description" content="Australia Suburbs list and their crime rate" />

        <meta property="og:title" content="Australia Suburbs List" />
        <meta property="og:url" content="https://redsuburbs.com.au/suburbs" />
        <meta property="og:description" content="Australia Suburbs list and their crime rate" />

        <meta name="twitter:card" content="summary_large_image" />
        <meta name="twitter:description" content="Australia Suburbs list and their crime rate" />
      </Helmet>

      <ContentPageHeader />
      <section className="Page__content">
        <h1 className="Page__title">Australia Suburbs List</h1>

        <table className={"SuburbsPage__suburbs " + (!sortAsc ? 'SuburbsPage__suburbs--dsc-sort' : '')}>
          <thead>
            <tr>
              <th className={"SuburbsPage__suburb-name-header " + (sortKey === 'name' ? 'SuburbsPage__active-key' : '')} onClick={() => changeSortKey('name')}>Suburb Name</th>
              <th className={"SuburbsPage__crime-rank-header " + (sortKey === 'cr' ? 'SuburbsPage__active-key' : '')} onClick={() => changeSortKey('cr')}>Crime Rank</th>
              <th className={"SuburbsPage__crime-rank-header " + (sortKey === 'pops' ? 'SuburbsPage__active-key' : '')} onClick={() => changeSortKey('pops')}>Population</th>
              <th className="SuburbsPage__actions-header">Actions</th>
            </tr>
          </thead>
          <tbody>
            {[...suburbs].sort((a, b) => sortSuburbs(a, b, sortKey, sortAsc)).slice((page - 1) * perPage, page * perPage).map(s => (
              <tr key={s.key} className="SuburbsPage__suburb">
                <td><Link className="SuburbsPage__suburb-name" to={getSuburbProfileLink(s.key)}>{s.name}</Link></td>
                <td className="SuburbsPage__suburb-rank">{s.cr}</td>
                <td className="SuburbsPage__suburb-rank">{s.pops}</td>
                <td className="SuburbsPage__suburb-actions"><Link className="SuburbsPage__suburb-btn btn btn--inline btn--red" to={getSuburbMapLink(s.bounds)}>On Map</Link></td>
              </tr>
            ))}
          </tbody>
        </table>

        <div className="SuburbsPage__pagination">
          { page == 1 ? null :
          <button className="btn btn--inline" onClick={() => changePage(-1)}>Previous</button>}
          &nbsp;
          Page #{page}
          &nbsp;
          <button className="btn btn--inline" onClick={() => changePage(+1)}>Next</button>
        </div>

        <p>
          <Link className="btn btn--red btn--inline" to="/">Back to Crime Map</Link>
        </p>
      </section>
      <ContentPageFooter />
    </div>
  );
}

