import React, {
  useEffect
} from 'react';
import { AttributionControl } from 'react-leaflet';

export default function Attribution() {
  const attrRef = React.createRef<L.Control.Attribution>();

  useEffect(() => {
    if(attrRef.current === undefined || attrRef.current === null) return;
    attrRef.current.setPrefix('&copy; <a href="https://redrealities.com">RedRealities PTY LTD</a>');
  });

  return <AttributionControl ref={attrRef} position="bottomleft" />;
}
