import {
  useState,
  useEffect
} from 'react';
import {
  useNavigate
} from 'react-router-dom';
import { 
  MapContainer,
  TileLayer,
  ZoomControl,
  GeoJSON
} from 'react-leaflet';
import * as L from 'leaflet';
import {
  LeafletMouseEvent
} from 'leaflet';
import tinycolor from 'tinycolor2';
import 'leaflet/dist/leaflet.css';

import { getSuburbProfileLink } from '../../Utils';

import { maxCrimeRank } from '../../Constants';
import { isMobileWidth } from '../../Utils';

import ICrimeData from '../../interfaces/ICrimeData';
import IGroupsIndex from '../../interfaces/IGroupsIndex';
import IGroupFeatureCollection from '../../interfaces/IGroupFeatureCollection';
import IAreaFeatureCollection from '../../interfaces/IAreaFeatureCollection';
import ISuburbFeature from '../../interfaces/ISuburbFeature';

import Attribution from '../Attribution/Attribution';

import './Map.css';

interface IMapProps {
  center: [number, number],
  zoom: number,
  selectedSuburb: string,
  onMapReady: (mapObject: L.Map) => void,
  onSuburbOver: (suburb: ISuburbFeature) => void,
  onSuburbOut: () => void,
}

const tStartColor = tinycolor('green');

function getSuburbColor(crimeRank: number) {
  crimeRank = Math.round(crimeRank / 10) * 10;
  const color = tinycolor(`rgba(255, 0, 0, ${crimeRank / 100})`);
  return color.toRgbString();
}

const zoomSwitchLvl = 10;

export default function Map(props: IMapProps) {
  const navigate = useNavigate();
  const [mapObject, setMapObject] = useState<L.Map | null>(null);
  const [bounds, setBounds] = useState<L.LatLngBounds>(L.latLngBounds(L.latLng(0,0), L.latLng(0,0)));
  const [zoom, setZoom] = useState<number>(props.zoom);

  const [groupsIndex, setGroupsIndex] = useState<IGroupsIndex | null>(null);
  const [loadedGroups, setLoadedGroups] = useState<string[]>([]);
  const [groups, setGroups] = useState<IGroupFeatureCollection[]>([]);
  const [areasIndex, setAreasIndex] = useState<IGroupsIndex | null>(null);
  const [loadedAreas, setLoadedAreas] = useState<string[]>([]);
  const [areas, setAreas] = useState<IAreaFeatureCollection[]>([]);

  useEffect(() => {
    (async () => {
      let response = await fetch('/data/groups/index.json');
      if(!response.ok) {
        alert('Failed to load suburb groups data!');
        throw new Error('Failed to load suburb groups data!');
      }

      let data: IGroupsIndex = await response.json();
      setGroupsIndex(data);

      response = await fetch('/data/areas/index.json');
      if(!response.ok) {
        alert('Failed to load areas data!');
        throw new Error('Failed to load areas data!');
      }

      data = await response.json();
      setAreasIndex(data);
    })();
  }, []);

  useEffect(() => {
    if(mapObject === null) return;

    let mapChangeTimeout: ReturnType<typeof setTimeout> | null = null;
    function handleMapChange() {
      if(mapObject === null) return;
      if(mapChangeTimeout !== null) clearTimeout(mapChangeTimeout);
      mapChangeTimeout = setTimeout(function() {
        const bounds = mapObject.getBounds();
        const zoom = mapObject.getZoom();
        setBounds(bounds);
        setZoom(zoom);
      }, 1000);
    }

    handleMapChange();
    mapObject.on('moveend', () => handleMapChange());
    mapObject.on('zoomend', () => handleMapChange());
  }, [mapObject]);

  // AREAS
  useEffect(() => {
    if(areasIndex === null) return;
    if(zoom > zoomSwitchLvl) return;
    const ids = Object.keys(areasIndex);
    //const loaded = areas.map(g => g.properties.area_code);
    let toLoad: string[] = [];
    ids.forEach(id => {
      // Check if group already loaded 
      if(loadedAreas.indexOf(id) !== -1) return;
      // Check if the group is within bounds
      const theBounds = L.latLngBounds(
        L.latLng(
          areasIndex[id][1],
          areasIndex[id][0],
        ),
        L.latLng(
          areasIndex[id][3],
          areasIndex[id][2],
        ),
      );
      if(!bounds.intersects(theBounds)) return;
      toLoad.push(id);
    }); 
    if(toLoad.length === 0) return;
    setLoadedAreas(oldItems => [...oldItems, ...toLoad]);
    
    console.log(`Areas to load: ${toLoad}`);
    // Loading areas
    (async () => {
      const newItems: IAreaFeatureCollection[] = [];
      for(let i = 0; i < toLoad.length; i++) {
        const response = await fetch(`/data/areas/${toLoad[i]}.geo.json`);
        if(!response.ok) {
          alert(`Failed to load area ${toLoad[i]}!`);
          throw new Error(`Failed to load area ${toLoad[i]}!`);
        }
        const data: IAreaFeatureCollection = await response.json();
        newItems.push(data);
      }
      setAreas((oldItems) => [...oldItems, ...newItems]);
    })();
  }, [zoom, bounds, groupsIndex, groups])

  // GROUPS
  useEffect(() => {
    if(mapObject === null) return;
    if(groupsIndex === null) return;
    if(zoom <= zoomSwitchLvl) return;
    const groupIds = Object.keys(groupsIndex);
    //const loadedGroups = groups.map(g => g.features[0].properties.group.toString());
    const groupsToLoad: string[] = [];
    groupIds.forEach(groupId => {
      // Check if group already loaded 
      if(loadedGroups.indexOf(groupId) !== -1) return;
      // Check if the group is within bounds
      const groupBounds = L.latLngBounds(
        L.latLng(
          groupsIndex[groupId][1],
          groupsIndex[groupId][0],
        ),
        L.latLng(
          groupsIndex[groupId][3],
          groupsIndex[groupId][2],
        ),
      );
      if(!bounds.intersects(groupBounds)) return;
      groupsToLoad.push(groupId);
    }); 
    if(groupsToLoad.length === 0) return;
    console.log(`Groups to load: ${groupsToLoad}`);
    setLoadedGroups(oldItems => [...oldItems, ...groupsToLoad]);
    // Loading the group
    (async () => {
      const newGroups: IGroupFeatureCollection[] = [];
      for(let i = 0; i < groupsToLoad.length; i++) {
        const response = await fetch(`/data/groups/${groupsToLoad[i]}.json`);
        if(!response.ok) {
          alert(`Failed to load group ${groupsToLoad[i]}!`);
          throw new Error(`Failed to load group ${groupsToLoad[i]}!`);
        }
        const data: IGroupFeatureCollection = await response.json();
        newGroups.push(data);
      }
      setGroups((oldGroups) => [...oldGroups, ...newGroups]);
    })();
  }, [zoom, bounds, groupsIndex, groups])

  function styleSuburb(suburb: ISuburbFeature | undefined) {
    //if(props.crimeData === null) return {};
    if(suburb === undefined) return {};
    return {
      stroke: false,
      fillOpacity: 0.6,
      fillColor: getSuburbColor(suburb.properties.crime_rank)
    };
  }

  function mouseOverHandler(event: LeafletMouseEvent, highlight = true) {
    const layer = event.layer;
    const suburb = event.layer.feature as ISuburbFeature;
    if(suburb.id !== undefined) props.onSuburbOver(suburb);

    if(highlight) {
      layer.setStyle({
        stroke: true,
        color: 'rgb(255, 0, 0)',
        opacity: 0.5
      });
      if(!L.Browser.ie && !L.Browser.opera && !L.Browser.edge) {
        layer.bringToFront();
      }
    }
  }

  function mouseOutHandler(event: LeafletMouseEvent) {
    const layer = event.layer;
    const suburb = event.layer.feature as ISuburbFeature;
    if(suburb.id !== undefined) props.onSuburbOut();

    layer.setStyle({
      stroke: false
    });
  }

  function mouseClickHandler(event: LeafletMouseEvent) {
    if(isMobileWidth()) {
      mouseOverHandler(event, false);
      return;
    }
    const suburb = event.layer.feature as ISuburbFeature;
    if(suburb.id !== undefined) navigate(getSuburbProfileLink(suburb.properties.key));
  }

  function dblMouseClickHandler(event: LeafletMouseEvent) {
    const suburb = event.layer.feature as ISuburbFeature;
    if(suburb.id !== undefined) navigate(getSuburbProfileLink(suburb.properties.key));
  }

  return (
  <MapContainer className="Map" 
    center={props.center}
    zoom={props.zoom}
    zoomControl={false}
    attributionControl={false}
    whenCreated={(map) => {
      setMapObject(map);
      props.onMapReady(map);
    }}
  >
    <TileLayer
      attribution='&copy; <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a>'
      url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
    />

    {zoom > zoomSwitchLvl ?
      groups.map(g =>
        <GeoJSON 
          key={g.features[0].properties.group}
          data={g} 
          style={styleSuburb}
          eventHandlers={{
            click: mouseClickHandler,
            dblclick: dblMouseClickHandler,
            mouseover: mouseOverHandler,
            mouseout: mouseOutHandler,
          }}
        />
      )
      :
      areas.map(a => 
        <GeoJSON 
        key={a.properties.area_code}
        data={a} 
        style={styleSuburb}
        />
      )
    }

    <ZoomControl position={'bottomright'} />
    <Attribution /> 
  </MapContainer>
  );
}
