import {
  useState
} from 'react';

import { ReactComponent as CloseIcon } from '../../images/x-icon.svg';

import './Legend.css';

export default function Legend() {
  const [visible, setVisible] = useState<boolean>(false);

  return (
    <div className="Legend">
    {!visible ? <button className="Legend__open-btn" onClick={() => setVisible(true)}>?</button> : (
      <div className="Legend__content">
        <h4 className="Legend__title">Legend</h4>
        <div className="Legend__ranks">
          <div className="Legend__rank Legend__rank--red">High Crime</div>
          <div className="Legend__rank">Low Crime</div>
        </div>

        <h5 className="Legend__sub-title">Crime Rank</h5>
        <p className="Legend__text">Number of offenses per population adjusted for severity of the crime.</p>

        <button className="Legend__close-btn" onClick={() => setVisible(false)}><CloseIcon/></button>
      </div>
    )}
    </div>
  )
}
