import React, {
  useState,
  useEffect
} from 'react';
import { 
  Link,
  useNavigate,
} from 'react-router-dom';
import { Helmet } from 'react-helmet-async';

import { 
  getSuburbMapLink,
  getSuburbProfileLink,
  useQuery
} from '../../Utils';

import ContentPageHeader from '../../components/ContentPageHeader/ContentPageHeader';
import ContentPageFooter from '../../components/ContentPageFooter/ContentPageFooter';

import ISuburb from '../../interfaces/ISuburb'; 

export default function SuburbsIndex() {
  const [suburbs, setSuburbs] = useState<ISuburb[]>([]);

  useEffect(() => {
    (async () => {
      const response = await fetch('/data/suburbs.json');
      if(!response.ok) {
        alert('Failed to load suburbs list!');
        throw new Error('Failed to load suburbs list!');
      }

      const data: ISuburb[] = await response.json();
      setSuburbs(data);
    })(); 
  }, []);

  return (
    <div className="SuburbsPage Page">
      <Helmet>
        <title>Australia Suburbs Index - RedSuburbs</title>
        <meta name="description" content="Australia Suburbs Index and their crime rate" />

        <meta property="og:title" content="Australia Suburbs Index" />
        <meta property="og:url" content="https://redsuburbs.com.au/suburbs" />
        <meta property="og:description" content="Australia Suburbs Index and their crime rate" />

        <meta name="twitter:card" content="summary_large_image" />
        <meta name="twitter:description" content="Australia Suburbs Index and their crime rate" />
      </Helmet>

      <ContentPageHeader />
      <section className="Page__content">
        <h1 className="Page__title">Australia Suburbs Index</h1>

        {[...suburbs].sort((a, b) => (a.name < b.name) ? -1 : (a.name > b.name) ? 1 : 0).map(s => (
          <div key={s.key}><Link to={getSuburbProfileLink(s.key)}>{s.name}</Link></div>
        ))}

      </section>
      <ContentPageFooter />
    </div>
  );
}

