import React, {
  useState,
  useEffect
} from 'react';
import * as L from 'leaflet';
import { 
  MapContainer,
  MapConsumer,
  TileLayer,
  GeoJSON,
  //Polygon as LeafletPolygon,
} from 'react-leaflet';
import { 
  Link,
  useParams
} from 'react-router-dom';
import { Helmet } from 'react-helmet-async';
import { ResponsivePie  } from '@nivo/pie'

import { maxCrimeRank } from '../../Constants';
import { 
  isMobileWidth,
  getSuburbMapLink,
  getSuburbProfileLink
} from '../../Utils';

import ISuburbProfile from '../../interfaces/ISuburbProfile';
import TCrimes from '../../types/TCrimes';

import ContentPageHeader from '../../components/ContentPageHeader/ContentPageHeader';
import ContentPageFooter from '../../components/ContentPageFooter/ContentPageFooter';

import './SuburbProfilePage.css';

function prepChartCrimeData(crimes: TCrimes) {
  const crime_types = Object.keys(crimes).filter(c => c !== 'total');
  const data = crime_types.sort((a, b) => crimes[b] - crimes[a])
    .map((crime_type, idx) => {
      const hue = Math.round(360 * (idx / crime_types.length));
      return {
        "id": crime_type,
        "label": crime_type,
        "value": crimes[crime_type],
        "color": `hsl(${hue}, 50%, 50%)`
      };
    });
  return data;
}

export default function SuburbProfilePage(props: {
  //suburbs: ISuburbFeature[] | null
  //borderingSuburbs: IBorderingSuburbs | null
}) {
  const { key } = useParams();
  const [suburb, setSuburb] = useState<ISuburbProfile | null>(null);

  useEffect(() => {
    (async () => {
      const response = await fetch(`/data/suburbs/${key}.json`);
      if(!response.ok) {
        alert('Failed to load suburb profile!');
        throw new Error('Failed to load suburb profile!');
      }

      const data: ISuburbProfile = await response.json();
      setSuburb(data);
    })();
  }, [key])
  //const params: ISuburbProfilePageParams = useParams();
  //const suburb = props.suburbs === null ? null : props.suburbs.find(s => s.properties.key === key);
  if(suburb === null) return (
    <div className="SuburbProfilePage Page">
      <ContentPageHeader />
      <section className="Page__content">
        <p>Loading suburb data...</p>
      </section>
    </div>
  );

  const crime = suburb.crime;
  const centerPoint = (
    L.bounds(
      L.point(suburb.bounds[1], suburb.bounds[0]),
      L.point(suburb.bounds[3], suburb.bounds[2])
    ).getCenter()
  );
  const center = L.latLng(centerPoint.x, centerPoint.y);

  return (
    <div className="SuburbProfilePage Page">
      <Helmet>
        <title>Crime rate in {suburb.name}, Australia - RedSuburbs</title>
        <meta name="description" content={`Crime rate profile of ${suburb.name} suburb`} />

        <meta property="og:title" content={`Crime rate in ${suburb.name}`} />
        <meta property="og:url" content={`https://redsuburbs.com.au/suburbs/${suburb.key}`} />
        <meta property="og:description" content={`Crime rate profile of ${suburb.name} suburb`} />

        <meta name="twitter:card" content="summary_large_image" />
        <meta name="twitter:description" content={`Crime rate profile of ${suburb.name} suburb`} />
      </Helmet>

      <ContentPageHeader />
      <section className="Page__content">
        <h1 className="Page__title">Crime rate in {suburb.name}, Australia</h1>
        <div className="SuburbProfilePage__population">Population: {suburb.pops}</div>
        <div className="SuburbProfilePage__total-crimes">Total crimes: {crime['total']}</div>
        <h2 className="Page__sub-title">Crime Rank: {suburb.cr}/{maxCrimeRank}</h2>

        <div className="SuburbProfilePage__crime-container grid">
          <div className="grid__col--span6">
            <h4 className="SuburbProfilePage__sub-title">Top crime types:</h4>
            <div className="SuburbProfilePage__crimes">
            {Object.keys(crime)
              .filter(c => c !== 'total')
              .sort((a, b) => crime[b] - crime[a])
              .map(crime_type => (
              <div key={crime_type} className="SuburbProfilePage__crime">
                <div className="SuburbProfilePage__crime-name">{crime_type}</div>
                <div className="SuburbProfilePage__crime-val">{crime[crime_type]}</div>
              </div>))}
            </div>

          </div>
          <div className="grid__col--span6">
            <MapContainer className="SuburbProfilePage__map" 
              center={center}
              zoom={14}
              zoomControl={false}
              attributionControl={true}
            >
              <TileLayer
                attribution='&copy; <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a>'
                url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
              />
              <GeoJSON 
              key={suburb.key}
              data={suburb.geojson} 
              style={{ color: '#e84917' }}
              /> 
              <MapConsumer>
              {(map) => {
                //map.setView(center, 14);
                map.fitBounds([
                  [suburb.bounds[1], suburb.bounds[0]],
                  [suburb.bounds[3], suburb.bounds[2]]
                ]);
                return null;
              }}
              </MapConsumer>
            </MapContainer>

            <h4 className="SuburbProfilePage__sub-title">Neighbouring Suburbs:</h4>
            <div className="SuburbProfilePage__bordering-suburbs">
            {suburb.borders.map(suburbBorder => {
              //const suburb = getSuburbByName(suburbName, props.suburbs);
              //if(suburb === null)
              //  return (<span key={suburbName} className="SuburbProfilePage__bordering-suburb">{suburbName}</span>);
              //else
              return (
                <Link key={suburbBorder.name} className="SuburbProfilePage__bordering-suburb" to={getSuburbProfileLink(suburbBorder.key)}>
                  {suburbBorder.name} ({suburbBorder.cr})
                </Link>
              );
            })}
            </div>
          </div>
          <div className="grid__col--span12">
            <div className="SuburbProfilePage__chart">
              <ResponsivePie 
                data={prepChartCrimeData(crime)}
                margin={!isMobileWidth() ?
                          { top: 40, right: 80, bottom: 80, left: 80  }
                        :
                          { top: 20, right: 20, bottom: 20, left: 20  }
                }
                innerRadius={0.5}
                padAngle={0.7}
                cornerRadius={3}
                activeOuterRadiusOffset={8}
                borderWidth={1}
                borderColor={{
                  from: 'color',
                    modifiers: [
                      [
                        'darker',
                        0.2
                      ]
                    ]
                }}
                enableArcLinkLabels={!isMobileWidth()}
                arcLinkLabelsSkipAngle={10}
                arcLinkLabelsTextColor="#333333"
                arcLinkLabelsThickness={2}
                arcLinkLabelsColor={{ from: 'color' }}
                arcLabelsSkipAngle={10}
                arcLabelsTextColor={{
                  from: 'color',
                    modifiers: [
                      [
                        'darker',
                        2
                      ]
                    ]
                }}
                defs={[
                  {
                    id: 'dots',
                    type: 'patternDots',
                    background: 'inherit',
                    color: 'rgba(255, 255, 255, 0.3)',
                    size: 4,
                    padding: 1,
                    stagger: true
                  },
                  {
                    id: 'lines',
                    type: 'patternLines',
                    background: 'inherit',
                    color: 'rgba(255, 255, 255, 0.3)',
                    rotation: -45,
                    lineWidth: 6,
                    spacing: 10
                  }
                ]}
                legends={[
                  // {
                  //   anchor: 'right',
                  //   direction: 'column',
                  //   justify: false,
                  //   translateX: 0,
                  //   translateY: 56,
                  //   itemsSpacing: 0,
                  //   itemWidth: 100,
                  //   itemHeight: 18,
                  //   itemTextColor: '#999',
                  //   itemDirection: 'left-to-right',
                  //   itemOpacity: 1,
                  //   symbolSize: 18,
                  //   symbolShape: 'circle',
                  //   effects: [
                  //     {
                  //       on: 'hover',
                  //       style: {
                  //         itemTextColor: '#000'
                  //       }
                  //     }
                  //   ]
                  // }
                ]}
              />
            </div>
          </div>
        </div>

        <Link className="btn btn--inline" to="/suburbs">Suburbs list</Link>&nbsp;
        <Link className="btn btn--red btn--inline" to={getSuburbMapLink(suburb.bounds)}>See on Crime Map</Link>
      </section>
      <ContentPageFooter />
    </div>
  );
}
