import React, {
  useState,
  useEffect,
  useMemo
} from 'react';

import {
  useLocation,
  useNavigate
} from 'react-router-dom';
import * as L from 'leaflet';

import Map from '../../components/Map/Map';
// import Feedback from '../../components/Feedback/Feedback';
import Legend from '../../components/Legend/Legend';
import SuburbQuickView from '../../components/SuburbQuickView/SuburbQuickView';
import SuburbSearch from '../../components/SuburbSearch/SuburbSearch';

import ISuburbFeature from '../../interfaces/ISuburbFeature';
import IGeoPlugin from '../../interfaces/IGeoPlugin';

import {
  useQuery
} from '../../Utils';

import './HomePage.css';


async function getUserLocation() {
  try {
    const response = await fetch('https://k6hsn2pcl7kxzwysf7b7x3s6xa0ldddm.lambda-url.us-east-1.on.aws/');
    // const response = await fetch('http://www.geoplugin.net/json.gp');
    // Melbourne
    // const response = await fetch('http://www.geoplugin.net/json.gp?ip=103.111.183.26');
    // Sydney
    // const response = await fetch('http://www.geoplugin.net/json.gp?ip=35.213.243.56');
    if(!response.ok) {
      console.log('Failed to detect user location');
      return null;
    }
    const data: IGeoPlugin = await response.json();
    return data;
  } catch(e) {
    return null;
  }
}

export default function HomePage() {
  const navigate = useNavigate();
  const [overSuburb, setOverSuburb] = useState<ISuburbFeature | null>(null);
  const [mapObject, setMapObject] = useState<L.Map | null>(null);

  const query = useQuery();
  const hasUrlLatLng = query.has('lat') && query.has('lng');
  let latlng: [number, number] = [-27.4594, 153.0289];
  let zoom = 12;
  if(hasUrlLatLng) {
    latlng = [Number(query.get('lat')), Number(query.get('lng'))]; 
  }
  if(query.has('zoom')) zoom = Number(query.get('zoom'));

  
  useEffect(() => {
    if(mapObject === null) return;

    let mapChangeTimeout: ReturnType<typeof setTimeout> | null = null;
    function handleMapChange() {
      if(mapObject === null) return;

      if(mapChangeTimeout !== null) clearTimeout(mapChangeTimeout);

      mapChangeTimeout = setTimeout(function() {
        const center = mapObject.getCenter();
        const zoom = mapObject.getZoom();
        navigate(`/?lat=${center.lat}&lng=${center.lng}&zoom=${zoom}`);
      }, 500);
    }

    mapObject.on('moveend', () => handleMapChange());
    mapObject.on('zoomend', () => handleMapChange());
  }, [mapObject, navigate]);

  async function mapReadyHandler(map: L.Map) {
    setMapObject(map);

    if(hasUrlLatLng) return;
    const geoipLatLng = await getUserLocation();
    //console.log(geoipLatLng);
    if(geoipLatLng === null || geoipLatLng.geoplugin_status < 200 || geoipLatLng.geoplugin_status >= 300) return;
    const center = L.latLng(
      parseFloat(geoipLatLng.geoplugin_latitude),
      parseFloat(geoipLatLng.geoplugin_longitude)
    );
    //console.log(center);
    map.setView(center, zoom);
  } 

  const crimeMap = useMemo(() => <Map 
    center={latlng}
    zoom={zoom}
    selectedSuburb={''} 
    onMapReady={mapReadyHandler}
    onSuburbOver={s => setOverSuburb(s)} 
    onSuburbOut={() => setOverSuburb(null)}
  />, []);

  return (
    <div className="HomePage Page Page--no-top-space">
      {crimeMap} 
      <SuburbQuickView suburb={overSuburb} />
      <SuburbSearch mapObject={mapObject} onSuburbSelected={s => console.log(s)} />
      <Legend />
      {/*<Feedback />*/}
    </div>
  );
}
