import {
  useLocation
} from 'react-router-dom';

import * as L from 'leaflet';

import ISuburbFeature from './interfaces/ISuburbFeature';
import TBounds from './types/TBounds';

export function isMobileWidth(width = 768) { return window.innerWidth <= width; }

export function useQuery() {
  return new URLSearchParams(useLocation().search);
}

export function getSuburbMapLink(bounds: TBounds) {
  const center = (
    L.bounds(
      L.point(bounds[1], bounds[0]),
      L.point(bounds[3], bounds[2])
    ).getCenter()
  );
  return `/?lat=${center.x}&lng=${center.y}&zoom=14`;
}

export function getSuburbProfileLink(suburbKey: string) {
  return '/suburbs/' + suburbKey;
}

export function getSuburbProfileLinkByName(name: string, suburbs: ISuburbFeature[] | null) {
  if(suburbs === null) return '/suburbs';
  const suburb = suburbs.find(s => s.properties.name === name);
  if(suburb === undefined) return '/suburbs';
  return '/suburbs/' + suburb.properties.key;
}

export function getSuburbByName(name: string, suburbs: ISuburbFeature[] | null) {
  if(suburbs === null) return null;
  const suburb = suburbs.find(s => s.properties.name === name);
  if(suburb === undefined) return null;
  return suburb;
}
